<template>
    <div class="cycleDetail">
        <div class="public-detail">
            <div class="important-info-box">
                <div class="public-titjz">方案信息</div>
                <div class="btns" v-if="cptisDiaodu">
                    <Button @click="accept">接受委托</Button>
                    <Button type="primary" @click="unacceptDialog">拒绝委托</Button>
                </div>
                <div class="btns" v-if="!cptisDiaodu">
                    <Button type="primary" @click="sendTrust">发送委托</Button>
                </div>
            </div>
        </div>
        <!-- 头部路线箭头 -->
        <line-header :lineHeaderData="cptlineHeaderData"></line-header>
        <div v-if="isGuilv" class="public-titjz">周期规律合同委托编号：{{cptentrustCode}}</div>
        <div v-if="!isGuilv" class="public-titjz">周期非规律合同委托编号：{{cptentrustCode}}</div>
        <div class="main-box">
            <div class="piblic-rate-tabs-box">
                <Tabs v-model="tabName" type="card">
                    <TabPane label="委托基本信息" name="基本信息">
                        <form-detailj ref="formDetailj" @handelLaw="handelLaw"></form-detailj>
                    </TabPane>
                    <TabPane label="单次委托信息" name="详细信息">
                        <form-detailx ref="formDetailx"></form-detailx>
                    </TabPane>
                </Tabs>
            </div>
        </div>
        <!--  拒绝理由弹框 -->
        <adt-my-modal
            :showModal="showItemModal"
            :title="itemTitle"
            @on-visible-change="visibleChange"
            @on-ok="handleItemModalOk"
        >
            <Form
                ref="formValidate"
                :model="remarkForm"
                :rules="remarkFormruleValidate"
                :label-width="80"
            >
                <FormItem label="拒绝理由" prop="remark">
                    <Input
                        v-model="remarkForm.remark"
                        type="textarea"
                        :autosize="{minRows: 2,maxRows: 5}"
                        placeholder="Enter something..."
                    ></Input>
                </FormItem>
            </Form>
        </adt-my-modal>
    </div>
</template>

<script>
import {
    apiOnceEntrustInfoAdd,
    apiModifyPeriodEntrustStatus
} from '@/api/logisticsCommission'
import moment from 'moment'
import lineHeader from './line-header/index'
import formDetailj from './FormDetailj'
import formDetailx from './FormDetailx'
import formCargoInfo from '@/mixins/formCargoInfo'
export default {
    name: 'cycleDetail',
    mixins: [formCargoInfo],
    components: {
        lineHeader,
        formDetailj,
        formDetailx
    },
    data() {
        return {
            showItemModal: false,
            itemTitle: '拒绝理由',
            isGuilv:true,
            remarkForm: {},
            remarkFormruleValidate: {
                remark: [
                    {
                        required: true,
                        message: 'Please enter a personal introduction',
                        trigger: 'blur'
                    },
                    {
                        type: 'string',
                        min: 2,
                        message: 'Introduce no less than 20 words',
                        trigger: 'blur'
                    }
                ]
            },
            // 表单
            tabName: '基本信息'
        }
    },
    computed: {
        cptlineHeaderData() {
            let arr = JSON.parse(this.$route.query.lineHeaderData)
            return arr
        },
        cptentrustCode() {
            let str = this.$route.query.entrustCode
            return str
        },
        cptexecutoryEntrustCode() {
            let str = this.$route.query.executoryEntrustCode
            return str
        },
        //零时判断角色的
        cptisDiaodu(){
            let str = this.$route.query.role
            if(str == 'admin'){
                return true
            }else{
                return false
            }
        },
    },
    created() {},
    methods: {
        // 判断规律还是非规律函数
        handelLaw(data){
            if(data == '周期性规律委托'){
                this.isGuilv = true
            }else{
                this.isGuilv = false
            }
        },
        // 弹框里面的取消按钮
        visibleChange(data) {
            this.showItemModal = data
        },
        // 弹框里面的确认按钮
        handleItemModalOk() {
            this.showItemModal = false
            this.unaccept()
        },
        //点击打开拒绝弹框
        unacceptDialog(name) {
            this.showItemModal = true
        },
        //我是接受委托
        accept(name) {
            let data = {
                entrustCode: this.cptentrustCode, //委托编号
                entrustStatus: '已接受' //委托状态
            }
            apiModifyPeriodEntrustStatus({
                data
            }).then(({ data }) => {
                this.$Message.success('操作成功')
            })
        },
        unaccept(name) {
            //拒绝接受委托
            let data = {
                entrustCode: this.cptentrustCode, //委托编号
                reason: this.remarkForm.remark,
                entrustStatus: '已拒绝' //委托状态
            }
            apiModifyPeriodEntrustStatus({
                data
            }).then(({ data }) => {
                this.$Message.success('操作成功')
            })
        },
        // 发送周期委托
        sendTrust(){
            let data = {
                entrustCode: this.cptentrustCode, //委托编号
                entrustStatus: '已下单' //委托状态
            }
            apiModifyPeriodEntrustStatus({
                data
            }).then(({ data }) => {
                this.$Message.success('操作成功')
                this.$router.push('/logisstics-commission/one-logistics/1')
            })
        }
    }
}
</script>
<style lang="scss" scoped>
.cycleDetail {
    @import './rate-tabs/tabs.scss';
    .important-info-box {
        display: flex;
        justify-content: space-between;
        /deep/.ivu-btn {
            margin-left: 20px;
        }
    }
    .main-box {
        padding: 20px;
        background: #fff;
    }
}
</style>